ul.timeline {
  list-style-type: none;
  position: relative;
}


ul.timeline:before {
  content: " ";
  background:rgba(183, 28, 28,0.4);
  box-shadow: 0 4px 4px 0 rgba(183, 28, 28,0.4);
  display: inline-block;
  position: absolute;
  left: 29px;
  width: 2px;
  height: 100%;
  z-index: 400;
}

ul.timeline > li {
  margin: 20px 0;
  padding-left: 20px;
}

ul.timeline > li:before {
  content: " ";
  background: rgba(183, 28, 28,0.8);
  box-shadow: 0 0 4px 4px rgba(183, 28, 28,0.3); 
  display: inline-block;
  position: absolute;
  border-radius: 50%;
  border: 3px solid rgba(183, 28, 28,0.2);
  left: 20px;
  width: 20px;
  height: 20px;
  z-index: 800;
}


ul.timeline span {
  font-style: italic;
  color: #b71c1c;
  font-size: 18px;
}

ul.timeline p,
h5 {
  padding-bottom: 15px;
}

