/* The Modal (background) */
.modal {
  position: fixed;
  /* Stay in place */
  padding-top: 100px;
  /* Location of the box */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: rgb(0, 0, 0);
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.9);
  /* Black w/ opacity */
}

.modal-section {
  margin: auto;
  display: block;
  margin-bottom: 40px;
  margin-top: -40px;
}

.modal-body,
.modal-content{
  background-color: rgba(0, 0, 0,0);
  color: white;
}

/* Add Animation - Zoom in the Modal */
.modal-content {
  animation-name: zoom;
  animation-duration: 0.6s;
}

.modal-content i {
  font-size: 450px;
}

#node{
  font-size: 400px;
}

.modal-body ul li{
  list-style-type: none;
  list-style: none;
} 

.modal-body ul{
  margin-top: -12px;
  line-height: 30px;
} 

.modal-body a {
  color: black;
}

@keyframes zoom {
  from {
    transform: scale(0);
  }

  to {
    transform: scale(1);
  }
}

/* The Close Button of modal*/
.closer {
  position: absolute;
  top: 15px;
  right: 35px;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
  text-decoration: none;
  color: white;
  opacity: 0.65;
}

.closer:hover,
.closer:focus {
  opacity: 1;
  text-decoration: none;
  cursor: pointer;
}

.modal-body i {
  padding-left: 2px;
  padding-right: 4px;
}

.modal-title h6{
  margin-bottom: 10px;
}

